@import '../../styles/mixins';

.home-page {
  &__splash,
  &__about,
  &__offer,
  &__call-to-action,
  &__news,
  &__references,
  &__contact,
  &__newsletter {
    position: relative;
    background-position: center center;
    overflow: hidden;
    padding-top: 50px;
    padding-bottom: 90px;
  }

  /* Splash */

  &__splash {
    background-image: url('../../images/splash.jpg');
    background-size: cover;
    background-attachment: fixed;
    z-index: 9;
    padding-bottom: 0;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.7);
      z-index: -9;
    }
  }

  /* About */

  &__about {
    @include flex(column, center, center);
  }

  /* Offer */

  &__offer {
    .offer-wrap {
      @include flex(row, flex-start, space-between);
      flex-wrap: wrap;
      padding: 0 20px;
    }
  }
  /* Call to action */
  &__call-to-action {
    background-color: var(--color-green-dark);
    @include flex(row, center, space-between);
  }

  &__contact {
    height: 35vh;
  }

  &__newsletter {
    background-color: var(--color-green-dark);

    .newsletter-section {
      @include flex(row, center, space-between);
      padding: 50px;

      @include md-max {
        @include flex(column-reverse, center, space-around);
      }
    }
  }
}
