@import '../../styles/mixins';

.cart {
  @include flex(column, flex-start, center);
  width: 320px;
  color: var(--color-dark);
  text-align: left;
  margin-top: 20px;

  @include md-max {
    width: 100%;
  }

  .img-wrap {
    margin-bottom: 27px;
    width: 320px;
    height: 212px;

    @include md-max {
      width: 100%;
      height: auto;
    }
  }

  h3 {
    &::after {
      content: '';
      display: block;
      height: 3px;
      width: 77px;
      background-color: var(--color-main);
      margin-top: 20px;
    }
  }

  .abstract {
    margin: 20px 0;
    height: 150px;
  }
}
